import { BasicColumn } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';

enum Api {
  addSafetyTools = '/po/safety-tools/create',
  getSafetyTools = '/po/safety-tools/get',
  getSafetyToolsCheckList = '/po/safety-tools-check/getBySafetyToolsId',
  //物料
  getMaterialPage = '/baseset/material/searchPage',
  //第三方
  getPartyPage = '/baseset/related-party/page',
}

//查询
export function getSafetyTools(params) {
  return defHttp.get({ url: Api.getSafetyTools, params });
}

// 新增
export function createSafetyTools(params) {
  return defHttp.post({ url: Api.addSafetyTools, params });
}
// 查询检查
export function getSafetyToolsCheckList(params) {
  return defHttp.get({ url: Api.getSafetyToolsCheckList, params });
}
export const getMaterialUrl = Api.getMaterialPage;
export const getPartyUrl = Api.getPartyPage;

