<template>
  <PageWrapper dense contentFullHeight fixedHeight  contentClass="flex" style="position: relative">
	<a-spin :spinning="formState.spinning" tip="Loading...">
    <div class="process-box">
      <div class="header-box">
          <div class="header-left">安全工具器</div>
          <div v-if="!disabledInput" class="header-right">
            <a-button style="margin-right: 10px;" @click="handleCancel">{{ '取消' }}</a-button>
            <a-button type="primary" @click="onFinish" :loading="formState.spinning">
              {{ '保存' }}</a-button
            >
          </div>
          <!-- <PrintButton style="margin-left: 10px" v-if="formState.id !='' " /> -->
        </div>
    <div class="content">
      <a-form
        :model="formState"
        name="basic"
        ref="formRef"
        autocomplete="off"
      >
        <a-card title="基本信息" :bordered="false">
          <a-row :gutter="32">
            <a-col :span="12">
              <a-form-item
                label="电站名称"
                name="formState.stationName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-input v-model:value="formState.stationName" disabled="true" />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="工器具名称"
                name="name"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '工器具名称不能为空。' }]"
              >
                <SearchBox
                  v-model:value="formState.name"
                  :columns="materialColumns"
                  :disabled="disabledInput"
                  :api="getMaterialUrl"
                  @search="
                    (value) => {
                      materialQueryName = value;
                    }
                  "
                  :vfield="`name`"
                  :pagination="true"
                  :validateFunc="
                    () => {
                      handleValid('name');
                    }
                  "
                  style="width: 100%"
                  @change="changeMaterial"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="物料编码"
                name="materialCode"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-input v-model:value="formState.materialCode" disabled="true" />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="物料全称"
                name="materialFullName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-input v-model:value="formState.materialFullName" disabled="true" />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="工器具编码"
                name="code"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-input v-model:value="formState.code" disabled="true" />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="规格型号"
                name="specification"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '规格型号不能为空' }]"
              >
                <a-input
                  v-model:value="formState.specification"
                  :disabled="disabledInput"
                  maxlength="32"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="生产厂家"
                name="factoryName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '生产厂家不能为空' }]"
              >
                <SearchBox
                  v-model:value="formState.factoryName"
                  :columns="factoryColumns"
                  :disabled="disabledInput"
                  :api="getPartyUrl"
                  :apiParamFunc="
                    () => {
                      return {
                        partyTypeCodes: 'manufacturer',
                        materialCategoryIds: formState.materialCategoryCode,
                      };
                    }
                  "
                  :vfield="`name`"
                  :pagination="true"
                  style="width: 100%"
                  :validateFunc="
                    () => {
                      handleValid('factoryName');
                    }
                  "
                  @change="
                    (id, item) => {
                      formState.factoryId = item.record.key;
                      formState.factoryName = item.record.name;
                    }
                  "
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="供应商"
                name="supplierName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '供应商不能为空' }]"
              >
                <SearchBox
                  v-model:value="formState.supplierName"
                  :columns="supplierColumns"
                  :disabled="disabledInput"
                  :api="getPartyUrl"
                  :apiParamFunc="
                    () => {
                      return {
                        partyTypeCodes: 'supplier',
                        materialCategoryIds: formState.materialCategoryCode,
                      };
                    }
                  "
                  :vfield="`name`"
                  :pagination="true"
                  style="width: 100%"
                  :validateFunc="
                    () => {
                      handleValid('supplierName');
                    }
                  "
                  @change="
                    (id, item) => {
                      formState.supplierId = item.record.key;
                      formState.supplierName = item.record.name;
                    }
                  "
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="生产日期"
                name="productionDate"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '生产日期不能为空' }]"
              >
                <a-date-picker
                  v-model:value="formState.productionDate"
                  valueFormat="YYYY-MM-DD"
                  style="width: 100%"
                  :disabled="disabledInput"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="购买日期"
                name="purchasingDate"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '购买日期不能为空' }]"
              >
                <a-date-picker
                  v-model:value="formState.purchasingDate"
                  valueFormat="YYYY-MM-DD"
                  style="width: 100%"
                  :disabled="disabledInput"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="检验周期(月)"
                name="cycle"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '检验周期(月)不能为空' }]"
              >
                <a-input-number
                  v-model:value="formState.cycle"
                  min="0"
                  max="99"
                  :disabled="disabledInput"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="计划下次检验日期"
                name="nextCheckDate"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '计划下次检验日期不能为空' }]"
              >
                <a-date-picker
                  valueFormat="YYYY-MM-DD"
                  v-model:value="formState.nextCheckDate"
                  style="width: 100%"
                  :disabled="formState.id != null && formState.id != ''"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="备注"
                name="remark"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-textarea
                  v-model:value="formState.remark"
                  row="3"
                  style="width: 100%"
                  maxlength="1024"
                  :disabled="disabledInput"
                />
            
              </a-form-item>
            </a-col>
          </a-row>
        </a-card>
        <a-card title="检验记录" :bordered="false">
          <BasicTable sticky @register="table" class="w-4/4 xl:w-12/12">
                <template #bodyCell="{ column, text, record }">
                  <template v-if="column.dataIndex === 'billCode'">
                    <a @click="openPage(`${record.safetyToolsCheckId}`)" target="_blank">
                      {{ text }}
                    </a>
                  </template>
                  <template v-if="column.dataIndex === 'file'">
                    <a @click="openPage(`${record.dtlIds}`)" target="_blank">
                      {{ text }}
                    </a>
                  </template>
                </template>
              </BasicTable>
        </a-card>
        <a-card title="报废信息" :bordered="false">
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="报废流程单号"
                name="scrapBillCode"
                :labelCol="{ span: 4 }"
                :wrapperCol="7"
                :rules="[{ required: false }]"
              >
                <a @click="openScrap">{{ formState.scrapBillCode }}</a>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="报废时间"
                name="scrapDate"
                :labelCol="{ span: 4 }"
                :wrapperCol="7"
                :rules="[{ required: false }]"
              >
                {{ formState.scrapDate }}
              </a-form-item>
            </a-col>
          </a-row>
        </a-card>
        <a-card title="附件上传" :bordered="false" class="file-upload-content">
          <UploadBox
            :width="800"
            :height="550"
            :maxCount="20"
            :maxSize="100 * 1024 * 1024"
            :application="`po`"
            :module="`safetytools`"
            :vmode="formState.status == 0 ? `box` : `view`"
            :bizId="formState.bizFileId"
          />
        </a-card>
      </a-form>
    </div>
  </div>
  </a-spin>
</PageWrapper>
</template>
<script lang="ts" setup>
  //form
  import { PageWrapper } from '/@/components/Page';
  import { reactive, ref, onMounted, toRaw,computed } from 'vue';
  import { urlToPath } from '/@/utils/route';
  // import PrintButton from '/@/components/Framework/Button/PrintButton.vue';
  import { FormInstance } from 'ant-design-vue';
  import SearchBox from '@/components/Framework/Combox/SearchBox.vue';
  import { BasicTable, useTable } from '@/components/Table';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import { buildShortUUID } from '@/utils/uuid';
  import { SysMessage } from '/@/hooks/web/useMessage';
  import { MsgManager } from '/@/message/MsgManager';
  import { closeCurrentTab } from '@/utils/route';
  import {
    getSafetyTools,
    createSafetyTools,
    getSafetyToolsCheckList,
    dtlColumns,
    getMaterialUrl,
    getPartyUrl,
    materialColumns,
    factoryColumns,
    supplierColumns,
  } from './create';
  import { addTabPage } from '@/utils/route';
  import { useUserStore } from '/@/store/modules/user';

  const userStore = useUserStore();
  const userInfo = toRaw(userStore.getUserInfo);
  const materialQueryName = ref('');
  const formRef = ref<FormInstance>();
    const disabledInput=computed(()=>{
    return formState.status != 0 || (formState.status==0 && 
    formState.personMemberId !='' && 
          formState.personMemberId.substring(0,32) != userInfo.userId);
  })
  const isLoaded = ref(false);
  let { params: query } = urlToPath() as any;
  const formState: any = reactive({
    id: query.id || '',
    stationName: query.stationName || '',
    stationId: query.stationId || '',
    stationCode: query.powerStationCode || '',
    stationOrganName: query.stationOrganName || '',
    stationOrganId: query.stationOrganId || '',
    lastCategoryName: '',
    name: '',
    materialId: '',
    materialName: '',
    materialCode: '',
    materialFullName: '',
    code: '',
    specification: '',
    factoryId: '',
    factoryName: '',
    supplierId: '',
    supplierName: '',
    productionDate: '',
    purchasingDate: '',
    materialCategoryCode: '',
    personMemberId:'',
    cycle: '',
    spinning:false,
    nextCheckDate: '',
    remark: '',
    scrapBillCode: '',
    scrapDate: '',
    status: 0,
    billCode: '',
    fillinDate: '',
    bizFileId: '',
  });

  const changeMaterial = (value, item) => {
    let row = item.record;
    if (formState.materialId != row.key) {
      formState.supplierId = '';
      formState.supplierName = '';
      formState.factoryId = '';
      formState.factoryName = '';
    }
    formState.materialCode = row.code;
    formState.materialId = row.key;
    formState.materialName = row.name;
    formState.name = row.name;
    formState.materialCategoryCode = row.materialCategoryCode;
    formState.materialFullName = getFullName(row);
  };

  const getFullName = (record) => {
    let name = '';
    if (record.categoryName1) {
      name += record.categoryName1 + '-';
      formState.lastCategoryName = record.categoryName1;
    }
    if (record.categoryName2) {
      name += record.categoryName2 + '-';
      formState.lastCategoryName = record.categoryName1;
    }
    if (record.categoryName3) {
      name += record.categoryName3 + '-';
      formState.lastCategoryName = record.categoryName1;
    }
    name += record.name;
    return name;
  };

  const onFinish = () => {
    const value = formRef.value;
    if (!value) {
      return;
    }
    formState.spinning=true;
    value
      .validateFields()
      .then(() => {
        createSafetyTools(formState)
          .then((rsp) => {
            if (rsp.result != '') {
              formState.id = rsp.result.id;
              formState.code = rsp.result.code;
              SysMessage.getInstance().success('保存成功');
              MsgManager.getInstance().sendMsg('safety-tools', {});
            } else {
              formState.status = 0;
              
              SysMessage.getInstance().error('保存失败');
            }
            formState.spinning=false;
          })
          .catch((e) => {
            formState.status = 0;
            formState.spinning=false;
            console.log(e);
          });
      })
      .catch((e) => {
        console.log(e);
        formState.spinning=false;
        formState.status = 0;
      });
  };
  const handleCancel = () => {
    closeCurrentTab();
    MsgManager.getInstance().sendMsg('safety-tools', {});
  };
  const openPage = (id) => {
    if (id) {
      // todo 跳转关联流程
      // router.push(`/po/safetytools/safetytoolscheck/create?id=` + dtlIds);
      addTabPage(`/po/safetytools/safetytoolscheck/create?processInstanceId=` + id);
    }
  };
  const openScrap = () => {
    if (formState.scrapId) {
      // todo 跳转关联流程
      // router.push(`/po/safetytools/safetytoolsscrap/create?id=` + formState.scrapId);
      addTabPage(`/po/safetytools/safetytoolsscrap/create?processInstanceId=` + formState.scrapId);
    }
  };

  const [table] = useTable({
    title: '',
    api: async () => {
      if (formState.id) {
        let dataObj = await getSafetyToolsCheckList({ safetyToolsId: formState.id });
        return dataObj.result;
      }
    },
    bordered: true,
    columns: dtlColumns,
    useSearchForm: false,
    showTableSetting: false,
    pagination: false,
  });

  const handleValid = (fieldName) => {
    if (isLoaded.value) {
      formRef?.value?.validate(fieldName);
    }
  };

  // global
  onMounted(async () => {
    getSafetyTools({ id: formState.id })
      .then((rsp) => {
        let rs = rsp.result;
        if (formState.id) {
          for (let key in rs) {
            formState[key] = rs[key];
          }
          formState.status = rs.edit ? 0 : 1;
          if (query.status) {
            formState.status = Number(query.status);
          }
        } else {
          formState.bizFileId = buildShortUUID();
        }
        // 页面加载完毕
        isLoaded.value = true;
      })
      .then(() => {});
  });
</script>

<style lang="less" scoped>

  .customer_require::before {
    content: '*';
    display: inline-block;
    margin-right: 4px;
    color: #ff4d4f;
    font-family: SimSun, sans-serif;
    font-size: 14px;
    line-height: 1;
  }

.content{
  margin: 10px;
}
</style>
